@import "common";
.page_solution {
  .section_hero {
    padding-top: grid_sz(0,11);
    background-image: url("/img/product/1.jpg");
    background-size: 1000px auto;
    background-position: left bottom;
    background-repeat: no-repeat;
    height: 100vh;


    .section_title {
      margin-bottom: grid_sz(0,1); }
    .section_eng {
      font-weight: 300; }
    .section_para {
      margin-top: grid_sz(0,3);
      margin-bottom: grid_sz(0,6); }
    .col_left {
      align-items: flex-start;
      width: grid_sz(8,7);
      padding-right: grid_sz(1,0); }
    .col_right {
      align-items: flex-start;
      width: grid_sz(5,5);
      text-align: left;
      border-left: solid 1px $color_grey_light;
      padding-left: grid_sz(1,1);
      li {
        margin-top: grid_sz(0,0.5);
        margin-bottom: grid_sz(0,0.5);
        h4 {
          @include trans; }
        .detail {
          color: $color_grey_light;
          margin-top: grid_sz(0,1);
          margin-bottom: grid_sz(0,1);
          max-height: 0px;
          overflow: hidden;
          opacity: 0;
          @include trans; }

        .btn_expand.plus {
          &:before {
            transform: translate(-50%, -50%) rotate(-180deg); }
          &:after {
            transform: translate(-50%, -50%) rotate(90deg); } }

        &.active {
          h4 {
            color: $color_brown; }

          .detail {
            max-height: 400px;
            opacity: 1; }

          .btn_expand.plus {
            &:before,&:after {
              background-color: $color_brown;
 }              // width: 20px
            &:before {
              transform: translate(-50%, -50%) rotate(0deg); }
            &:after {
              transform: translate(-50%, -50%) rotate(180deg); } } } } }


    @include range_width(0px,800px) {
      .col_left,.col_right {
        width: 100%;
        min-height: 50vh; } } }


  //soluiton 
  ////program 
  //  //service 
  .btn_expand.plus {
    @include size(14px);
    position: absolute;
    left: grid_sz(-0.5,0);
    top: 10px;
    cursor: pointer;

    &:before,&:after {
      content: "";
      display: block;
      width: 100%;
      height: 2px;
      background-color: $color_grey;
      @include ab_center;
      transform-origin: center center;
      @include trans; } }



  .btn_expand.cross {
    @include size(46px);
    position: absolute;
    right: grid_sz(0,1);
    cursor: pointer;
    &:after,&:before {
      content: '';
      display: block;
      transform-origin: center center;
      @include ab_center;
      width: 100%;
      height: 2px;
      background-color: #FFF;
      transition: 0.5s;
      @include trans; }
    &:after {
      transform: translate(-50%,-50%) rotate(90deg); } }


  .section_solution {
    display: flex;
    flex-direction: column;
    padding-top: 0px;
    padding-bottom: 0px;
    min-height: initial;


    &.active {
      .main_info {
        border-bottom: none;
        .info * {
          color: $color_grey; }
        &:before {
          background-color: white; } }
      .btn_expand.cross {
        &:after,&:before {
          background-color: $color_grey; }
        &:before {
          transform: translate(-50%,-50%)  rotate(135deg); }
        &:after {
          transform: translate(-50%,-50%)  rotate(225deg); } }

      .program_list {
        @include toggle_height_active;
        margin-top: grid_sz(0,1); }




      @include toggle_height;
      &.active {
 } }        // margin-top: grid_sz(0,1)



    .main_info {
      background-image: url("/img/product/2.jpg");
      background-size: auto 100%;
      background-position: right top;
      background-color: $color_brown;
      border-bottom: solid 3px darken($color_brown,16);
      box-sizing: border-box;


      &:before {
        @include pseudo_ab_full;
        background-color: transparent;
        @include trans; }

      .btn_expand.no_content {
        opacity: 0.3;
        cursor: initial; }

      .container.flex {
        justify-content: flex-start;
        min-height: initial;
        padding-top: grid_sz(0,2);
        padding-bottom: grid_sz(0,2); }

      .cover {
        @include size(140px);
        border-radius: 50%; }
      .info {
        margin-left: grid_sz(0,2);
        text-align: left;
        * {
          color: white; } } }

    //
    .program_list {

      @include toggle_height;

      & > li {
        vertical-align: middle;
        text-align: left;
        padding: grid_sz(0,1);
        border-top: rgba($color_grey,0.4);
        border-bottom: rgba($color_grey,0.4);
        @include trans;
        position: relative;

        .tag {
          display: inline-block;
          margin-right: grid_sz(0,1);
          background-color: $color_grey_dark;

          // +flex_center
          border-radius: 50%;
          color: white;
          @include size(36px);
          vertical-align: middle;
          position: relative;
          span {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-35%,-50%); } }

        .program_title {
          display: inline-block;
          vertical-align: middle; }

        .btn_expand {
          // margin-right: grid_sz(1,0)

          // float: right
          position: absolute;
          right: 30px;
          @include size(24px);
          top: 36px;
          cursor: pointer;
          transform: translateY(-50%);

          &.no_content {
            opacity: 0.3;
            cursor: initial; }
          i {
            transform-origin: center center;
            font-size: 26px;
            @include ab_center;
            @include trans; } }

        //
        &.active {
          background-color: $color_brown;
          padding-bottom: 0px; //more info 
          .program_box {
            @include toggle_height_active;
            margin-top: grid_sz(0,1); }
          .program_title {
            color: white; }
          .tag {
            background-color: white;
            color: $color_brown; }
          .btn_expand {
            i {
              transform: translate(-70%,-50%) rotate(180deg);
              color: white; } } } } }

    //
    .program_box {
      background-color: white;
      margin-left: grid_sz(0,-1);
      margin-right: grid_sz(0,-1);
      @include toggle_height_hide;

      & > .container.flex {
        align-items: flex-start;
        padding-left: 0px;
        padding-right: 0px; }
      .col_left,.col_right {
        align-items: flex-start;

        .program_sub_title {
          margin-bottom: grid_sz(0,2);
          &:after {
            content: "";
            margin-top: grid_sz(0,1);
            display: block;
            width: 30px;
            border-bottom: solid 1px $color_grey; } }
        p {
          margin-bottom: grid_sz(0,2); } }
      .col_right {
        background-color: #E8ECEF; }
      .service_type {

        .btn_expand {
          position: absolute;
          right: 20px;
          top: 50%;
          // transform: translateY(-50%)
          @include size(16px);

          &:before,&:after {
            background-color: white; }
          &:before {
            transform: translate(-50%,-50%) rotate(90deg); }
          &:after {
            transform: translate(-50%,-50%) rotate(180deg); } }



        li {
          .toggle_part {
            height: grid_sz(0,2);

            background-color: $color_brown;
            display: flex;
            align-items: center;
            color: white;
            width: 100%;
            padding-left: grid_sz(0,1);
            padding-right: grid_sz(0,1); }


          .info_part {
            padding: grid_sz(0,1);
            padding-top: 8px;
            padding-bottom: 0px;
            text-align: left;
            max-height: 0px;
            overflow: hidden;
            @include trans; }


          &.active {
            @include toggle_height_active;

            .toggle_part {
              border-bottom: solid 4px lighten($color_brown,7); }
            .info_part {
              max-height: 400px;
              padding: grid_sz(0,1); }
            .btn_expand {
              &:before {
                transform: translate(-50%,-50%) rotate(45deg); }
              &:after {
                transform: translate(-50%,-50%) rotate(135deg); } } } } }



      .more_info {
        background-color: $color_brown;
        color: white;
        padding: grid_sz(0,1) grid_sz(0,2); } } } }
